.App {
    margin: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    background-image: url("https://images.unsplash.com/photo-1595785921064-87c2a7f018f6?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
    background-size: cover; /* for IE9+, Safari 4.1+, Chrome 3.0+, Firefox 3.6+ */
    min-width: 100%;
    min-height: 100%;
    position: fixed;
    clear: inherit;

  }
  .heading{
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    color: wheat;
    left: 50px;
    image-rendering: pixelated;
  }

  .about{
    color: white;
    text-align: left;
    padding-top:8%;
    letter-spacing: inherit;
  }
  .footing{
    color: rgb(233, 199, 159);
    text-align: center;
    padding-top:5%;
    letter-spacing: inherit;
  }

  
 